:root {
  --color-brand--1: #ffb545;
  --color-brand--2: #00c46a;
  --color-dark--1: white;
  --color-dark--2: white;
  --color-light--1: #252525;
  --color-light--2: #252525;
  --color-light--3: white(214, 222, 224);
  --global-shadow: 6px 10px 29px -12px black;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--color-light--2);
  height: 100vh;
  overscroll-behavior-y: none;
  background-color: #fff;
  flex-flow: wrap;
  align-content: space-around;
  padding: 2.5rem;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  display: flex;
}

.full_arrow {
  width: 34px;
  height: 34px;
  box-shadow: var(--global-shadow);
  background-color: var(--color-brand--2);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .1s;
  display: flex;
  position: fixed;
  transform: translateY(-4rem);
}

.full_arrow_2 {
  top: 4px;
  right: 9px;
}

.full_arrow_1 {
  top: 4px;
  left: 8px;
}

a:link, a:visited {
  color: var(--color-brand--1);
}

.sidebar {
  background-color: var(--color-dark--1);
  flex-direction: column;
  flex-basis: 50rem;
  padding: 3rem 5rem 4rem;
  display: flex;
}

.logo {
  height: 40px;
  align-self: center;
}

.workouts {
  height: 77vh;
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
}

.workouts::-webkit-scrollbar {
  width: 0;
}

.workout {
  background-color: var(--color-dark--2);
  cursor: pointer;
  border-radius: 11px;
  flex-direction: column;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: .75rem 1.5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 2.25rem;
  display: flex;
}

svg {
  transition: all .3s;
}

.delete:hover, .edit:hover {
  fill: #fff;
}

.all_svg {
  position: relative;
  top: -11px;
  right: -18px;
}

.main__div {
  flex-wrap: wrap;
  justify-content: flex-start;
  display: flex;
}

.workout--running {
  border-left: 5px solid var(--color-brand--2);
}

.workout--cycling {
  border-left: 5px solid var(--color-brand--1);
}

.workout__title {
  grid-column: 1 / -1;
  font-size: 1.7rem;
  font-weight: 600;
}

.workout__details {
  align-items: baseline;
  display: flex;
}

.workout__icon {
  height: .28rem;
  margin-right: .2rem;
  font-size: 1.8rem;
}

.workout__value {
  font-size: 1.5rem;
}

.workout__unit {
  color: var(--color-light--1);
  text-transform: uppercase;
  font-size: 1.1rem;
  font-weight: 800;
}

.form {
  background-color: var(--color-dark--2);
  box-shadow: var(--global-shadow);
  height: 9.25rem;
  border-radius: 3px;
  grid-template-columns: 1fr 1fr;
  gap: .5rem 2.5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 2.75rem;
  transition: all .3s ease-in;
  display: grid;
}

.form.hidden {
  height: 0;
  opacity: 0;
  margin-bottom: 0;
  padding: 0 2.25rem;
  transform: translateY(-30rem);
}

.form__row {
  align-items: center;
  display: flex;
}

.form__row--hidden {
  display: none;
}

.form__label {
  color: #000;
  flex: 0 0 50%;
  font-size: 1.5rem;
  font-weight: 600;
}

.form__input {
  width: 100%;
  color: var(--color-brand--2);
  background-color: var(--color-light--3);
  box-shadow: var(--global-shadow);
  border: none;
  border-radius: 3px;
  padding: .3rem 1.1rem;
  font-family: inherit;
  font-size: 1.4rem;
  transition: all .2s;
}

.form__input:focus {
  background-color: #fff;
  outline: none;
}

.form__btn {
  width: 300px;
  height: 37px;
  padding: .3rem rem 1.1rem;
  background-color: var(--color-brand--2);
  box-shadow: var(--global-shadow);
  border: none;
  border-radius: 3px;
  margin: 14px -12px;
  font-family: inherit;
  font-size: 1.7rem;
  font-weight: 800;
  transition: all .2s;
}

.copyright {
  text-align: center;
  color: var(--color-light--1);
  margin-top: auto;
  font-size: 1.3rem;
}

.twitter-link:link, .twitter-link:visited {
  color: var(--color-light--1);
  transition: all .2s;
}

.twitter-link:hover, .twitter-link:active {
  color: var(--color-light--2);
}

#map {
  height: 100%;
  flex: 1;
}

@media (width >= 200px) and (width <= 500px) {
  body {
    overscroll-behavior-y: none;
    color: var(--color-light--2);
    background-color: #fff;
    flex-direction: column-reverse;
    align-content: stretch;
    padding: 0;
    font-family: Manrope, sans-serif;
    font-weight: 400;
    display: flex;
  }

  .popup_message {
    height: 19px;
    color: #fff;
    z-index: 2147483647;
    box-shadow: var(--global-shadow);
    border-radius: 3px;
    justify-content: center;
    align-items: center;
    padding: 15px 8px;
    font-size: 13px;
    font-weight: 600;
    transition: all .3s;
    display: flex;
    position: absolute;
    top: 6px;
    right: 110px;
    transform: translateY(-5rem);
  }

  .workout__title {
    grid-column: 1 / -1;
    font-size: 14px;
    font-weight: 600;
  }

  .workout__icon {
    margin-left: 9px;
    font-size: 15px;
  }

  .workout__value {
    font-size: 45px;
    font-weight: bolder;
  }

  .workout__unit {
    font-size: 9px;
  }

  .workoutList {
    width: 100%;
    flex-wrap: wrap;
    padding: 10px 14px;
    display: flex;
  }

  .form {
    background-color: var(--color-dark--2);
    width: 34rem;
    height: 12.6rem;
    border-radius: 3px;
    flex-direction: column;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: stretch;
    margin: 12px;
    display: flex;
  }

  .workout {
    background-color: var(--color-dark--2);
    width: 95px;
    cursor: pointer;
    box-shadow: var(--global-shadow);
    border-radius: 3px;
    flex-direction: column;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 10px;
    padding: 5px 4px;
    display: flex;
  }

  .form__input {
    width: 152px;
    height: 37px;
    background-color: var(--color-light--3);
    border: none;
    border-radius: 3px;
    margin: 2px -12px;
    padding: .3rem 1.1rem;
    font-family: inherit;
    font-size: 1.7rem;
    font-weight: 800;
    transition: all .2s;
  }

  .form__label {
    flex: 0 0 50%;
    font-size: 1.7rem;
    font-weight: 800;
  }

  .sidebar {
    background-color: var(--color-dark--1);
    height: 8%;
    border-bottom: 4px solid #ababab;
    flex-direction: column;
    flex-basis: 0;
    place-content: center;
    padding: 10px 0;
    transition: all .3s ease-in;
    display: flex;
  }

  .workouts {
    height: 89vh;
    flex-direction: column;
    align-items: center;
    list-style: none;
    display: flex;
    overflow-x: hidden;
    overflow-y: scroll;
  }
}

.leaflet-popup .leaflet-popup-content-wrapper {
  background-color: var(--color-dark--1);
  color: var(--color-light--2);
  border-radius: 3px;
  padding-right: .6rem;
}

.leaflet-popup .leaflet-popup-content {
  font-size: 1.5rem;
}

.leaflet-popup .leaflet-popup-tip {
  background-color: var(--color-dark--1);
}

.running-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--2);
}

.cycling-popup .leaflet-popup-content-wrapper {
  border-left: 5px solid var(--color-brand--1);
}

